<template>
  <div class="plan">
    <VanLoading size="24px">加载中，请稍后...</VanLoading>
  </div>
</template>

<script>
import { getSearch } from '@/utils'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'

export default {
  components: {
  },
  data () {
    return {
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  created () {
    // 授权完成需要回来，埋完点之后直接跳转,1: 文本,3: 图文,除此之外都是错的
    if (![1, 3].includes(Number(getSearch('msg_type'))) || !getSearch('redirect_url')) {
      this.$dialog({
        message: 'msg_type或者redirect_url拼接的不对,无法进行下一步'
      }).then((res) => {
        console.log(res)
      })
      return
    }
    const {
      id,
      unionid,
      wx_body: { openid }
    } = this.userInfo
    const { link, host, protocol, ...rest } = getSearch()
    this.$log.update({
      openid,
      unionid,
      mid: id,
      current_page: location.href,
      alternate: JSON.stringify(rest) // 偶现alternate字段缺失
    })
    const query = {
      module_part: '130_syllyy_h5_00001',
      module_ori: '公众号客服消息-文本链接点击统计'
    }
    if (Number(getSearch('msg_type')) === 3) {
      query.module_ori = '公众号客服消息-图文链接点击统计'
      query.module_part = '130_syllyy_h5_00002'
    }
    this.$log.click(query)
    // 存在没有埋点成功直接跳转的情况（偶现）
    setTimeout(() => {
      window.location.href = decodeURIComponent(getSearch('redirect_url'))
    }, 1000)
  },
  methods: {
  }
}
</script>
<style lang="scss" scoped>
.plan {
  text-align: center;
  height: 100vh;
  line-height: 100vh;
}
</style>
